@import '../common/colors';

.chart{
  position: relative;
}

.chart > figcaption {
  font-weight: 500;
  color: var(--colorBlack);
  margin-bottom: 20px;
}

.bar {
  fill: var(--colorLightGreen);
  height: 21px;
  transition: fill .3s ease;
  cursor: pointer;
}

.bar > text{
  fill: var(--colorGreyText);
  margin-left: 20px;
}

.bar:hover,
.bar:focus {
  fill: var(--colorDarkerGreen) !important;
}

.bar:hover text,
.bar:focus text {
  fill: var(--colorDarkerGreen);
}



